<template>
  <div>
    <h1>{{ msg }}</h1>
  </div>
</template>

<script>
  export default {
    name: 'goodbye',
    props: [ 'name' ],
    computed: {
      msg () {
        return `Goodbye, ${ this.name }!`
      }
    }
  }
</script>

<style lang="scss" scoped>

</style>



